<template>
<div class="page my-border">
    <div id="drawing" class="canvas">
    </div>
    <div class="code-area">
        <div><button @click="makeCode()">Make {{language.name}} Code</button></div>
        <highlightjs :language="language.id" :code="codes" />
    </div>
</div>
</template>

<script>
import {
    ref,
    onMounted
} from "vue";
import {
    SVG
} from "@svgdotjs/svg.js";
import "@svgdotjs/svg.draggable.js";
import {
    v4 as uuidv4
} from "uuid";
import MyNodeButtonV2 from "@/models/MyNodeButtonV2.js";
import MyNodeFunctionAlert from "@/models/MyNodeFunctionAlert.js";
//import MyNodeFunctionAdd from "@/models/MyNodeFunctionAdd.js";
import MyNodeVarString from "@/models/MyNodeVarString.js";
//import MyNodeVarInteger from "@/models/MyNodeVarInteger.js";
import Sockets from "@/values/sockets.js";
import {
    translateJS
} from "@/logics/interpreter_js.js";
import {
    translateJava
} from "@/logics/interpreter_java.js";
import {
    translatePy
} from "@/logics/interpreter_python.js";
import U from "@/logics/utils.js";

export default {
    props: {
        language: Object
    },
    setup(props) {
        const codes = ref("");
        const allPath = new Map();
        const network = {};
        const variableMaps = [new Map(), new Map()];
        const codeTree = {
            type: "class",
            id: "class",
            name: "Test",
            children: []
        };
        const triggerNodes = [];
        let buttonAction = null;

        let constructorClass = "";
        if (props.language.id === "javascript") {
            constructorClass = "constructor";
        } else if (props.language.id === "java") {
            constructorClass = "Test";
        } else if (props.language.id === "python") {
            constructorClass = "__init__";
        }

        network.execConnections = new Map(); //out_socket_id -> in_socket
        network.valueConnections = new Map(); //in_socket_id -> out_socket
        network.nodes = []; //all added chips
        network.end = function () {
            const codeText = [""];

            if (props.language.id === "javascript") {
                translateJS(codeTree, 0, codeText, variableMaps, null);
                codes.value = codeText[0];

                const button = document.getElementById(triggerNodes[0].id);
                let entrypoint = "";
                var instance1;
                entrypoint += "instance1 = new Test();";
                eval(codes.value + entrypoint);
                button.removeEventListener("click", buttonAction);
                buttonAction = function () {
                    instance1.buttonClicked();
                };
                button.addEventListener("click", buttonAction);

            } else if (props.language.id === "java") {
                translateJava(codeTree, 0, codeText, variableMaps, null);
                codes.value = codeText[0].trim();

            } else if (props.language.id === "python") {
                translatePy(codeTree, 0, codeText, variableMaps, null);
                codes.value = codeText[0].trim();
            }

        };
        network.refresh = function () {
            variableMaps[0].clear();
            variableMaps[1].clear();

            codeTree.children.splice(0, codeTree.children.length);
            this.scanNodes();

            triggerNodes.forEach(function (node) {
                node.execute();
            });
            console.log("Remake!!!");
        };
        network.scanNodes = function () {
            //make default constructor after declaring instance variables
            const constructor = {
                type: "method",
                id: uuidv4(),
                name: constructorClass,
                params: [],
                children: []
            };
            //find variable chips
            network.nodes.forEach(function (node) {
                if (node.buildTree) {
                    node.buildTree(codeTree, constructor);
                }
            });
            codeTree.children.push(constructor);
            U.parseTreeNodeIds(codeTree, variableMaps, null);
            //console.log(variableMaps[0].keys());
            //console.log(variableMaps[1].keys());
        };
        const state = {
            makingConnection: false,
            startSocket: null,
            endSocket: null,
            canDrag: true,
            finishConnection: null
        };

        onMounted(function () {
            const div = document.getElementById("drawing");
            while (div.firstChild) {
                div.removeChild(div.firstChild);
            }

            const draw = SVG().addTo("#drawing").size("70vw", "100vh");
            const drawingPath = draw.path("M 0 0 L 0 0");
            drawingPath.stroke({
                color: "#d61c00",
                width: 5,
                linecap: "round",
                linejoin: "round"
            });
            drawingPath.hide();

            const robotParent = draw.nested();
            //const robot = robotParent.use("Layer_1", "assets/Robot.svg");
            const request = new XMLHttpRequest();
            request.open("GET", "assets/Robot.svg");
            request.responseType = "text";
            request.addEventListener("load", function () {
                if (request.readyState === request.DONE) {
                    if (request.status === 200) {
                        const robot = robotParent.svg(request.response);
                        robot.width(200);
                        robot.draggable();
                        robot.animate({
                            duration: 1000,
                            delay: 500,
                            when: "now",
                            swing: true,
                            times: 1000
                        }).attr({
                            fill: "#f03"
                        })
                    }
                }
            });
            request.send(null);

            const finishConnection = function () {
                if (state.makingConnection) {
                    if (!state.endSocket) {
                        drawingPath.hide();
                    } else {
                        drawingPath.hide();
                        if (state.startSocket.node === state.endSocket.node) {
                            alert("Cannot make connection on the same node!");
                        } else if (network.execConnections.has(state.startSocket.id)) {
                            alert("Output socket can only have 1 connection!");
                        } else if (network.execConnections.get(state.startSocket.id) === state.endSocket.id) {
                            alert("Cannot make duplicate connection!");
                        } else if (state.startSocket.type !== state.endSocket.type) {
                            alert("Not same type!");
                        } else {
                            console.log("make connection!");
                            const pathId = uuidv4();
                            const path = draw.path("M " + (state.startSocket.node.svg.x() + state.startSocket.rx) + " " + (state.startSocket.node.svg.y() + state.startSocket.ry) + " L " + (state.endSocket.node.svg.x() + state.endSocket.rx) + " " + (state.endSocket.node.svg.y() + state.endSocket.ry));
                            path.stroke({
                                color: "#a63c01",
                                width: 5,
                                linecap: "round",
                                linejoin: "round"
                            });
                            allPath.set(pathId, path);

                            state.startSocket.node.addConnection(pathId, state.startSocket, state.endSocket);
                            state.endSocket.node.addConnection(pathId, state.startSocket, state.endSocket);
                            state.startSocket.node.svg.front();
                            state.endSocket.node.svg.front();
                            if (state.startSocket.type === Sockets.exec.name) {
                                network.execConnections.set(state.startSocket.id, state.endSocket);
                            } else {
                                network.valueConnections.set(state.endSocket.id, state.startSocket);
                            }
                            state.endSocket.hideInput();
                        }
                    }
                }
                //Clear everything
                if (state.startSocket) {
                    state.startSocket.initColor();
                }
                if (state.endSocket) {
                    state.endSocket.initColor();
                }
                state.makingConnection = false;
                state.startSocket = null;
                state.endSocket = null;
                state.canDrag = true;
                console.log("finishConnection");
            };
            state.finishConnection = finishConnection;

            draw.on("mouseup", finishConnection);
            draw.on("mousemove", function (e) {
                if (state.makingConnection) {
                    const startX = state.startSocket.node.svg.x() + state.startSocket.rx;
                    const startY = state.startSocket.node.svg.y() + state.startSocket.ry;
                    const endX = e.offsetX;
                    const endY = e.offsetY;
                    drawingPath.plot("M " + startX + " " + startY + " L " + endX + " " + endY);
                    drawingPath.show();
                }
            });

            const node1 = new MyNodeButtonV2(draw, network, state, allPath, "Button v2");
            node1.addSocket(Sockets.exec.name, "out");
            node1.addSocket(Sockets.int.name, "out");
            node1.setLang(props.language.id);
            triggerNodes.push(node1);

            const node2 = new MyNodeVarString(draw, network, state, allPath, "myStringValue");
            node2.addSocket(Sockets.exec.name, "in");
            node2.addSocket(Sockets.exec.name, "out");
            node2.addSocket(Sockets.string.name, "out");
            node2.addSocket(Sockets.string.name, "in");

            const node3 = new MyNodeFunctionAlert(draw, network, state, allPath, "Alert()");
            node3.addSocket(Sockets.exec.name, "in");
            node3.addSocket(Sockets.exec.name, "out");
            node3.addSocket(Sockets.string.name, "in");

            // const node4 = new MyNodeFunctionAdd(draw, network, state, allPath, "Add()");
            // node4.addSocket(Sockets.int.name, "in");
            // node4.addSocket(Sockets.int.name, "in");
            // node4.addSocket(Sockets.int.name, "out");

            // const node5 = new MyNodeVarInteger(draw, network, state, allPath, "myIntegerValue");
            // node5.addSocket(Sockets.exec.name, "in");
            // node5.addSocket(Sockets.exec.name, "out");
            // node5.addSocket(Sockets.int.name, "out"); //Variable block out socket is always outputting a Pointer!
            // node5.addSocket(Sockets.int.name, "in");

            network.nodes.push(node1);
            network.nodes.push(node2);
            network.nodes.push(node3);
            // network.nodes.push(node4);
            // network.nodes.push(node5);
        });

        const makeCode = function () {
            network.refresh();
        };

        return {
            codes,
            makeCode
        };
    }
}
</script>

<style scoped>
.canvas {
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

.my-border {
    border-style: solid;
    border-color: #fff;
    border-radius: 12px;
}

.page {
    display: flex;
}

.code-area {
    width: 100%;
    color: #fff;
    text-align: left;
    overflow-x: scroll;
}
</style>
