import U from "@/logics/utils.js";

export function translatePy(node, level, display, temp, parentId) {
    //node -> JSON node tree
    //level -> 0
    //display -> [""]
    //temp[0] -> classVars:Map [classVarId, classVar]
    //temp[1] -> localVars:Map [methodId, [localVarId, localVar]]
    //parentId -> might be undefined
    let indent = U.getIndent(level);
    let addPart = "";

    if (node.type === "class") {
        addPart += indent + node.type + " " + node.name + ":\n";
    } else if (node.type === "method") {
        //Python does not have return type on function
        addPart += indent + "def " + node.name + "(self";
        if (node.params) {
            node.params.forEach(function (param) {
                addPart += ", " + param.name;
            });
        }
        addPart += "):\n";

    } else if (node.type === "init") {
        if (parentId === "class") {
            //Python does not need to declare class variables at class level
        } else {
            addPart += indent + node.left.name;
            if (node.right) {
                addPart += " = " + U.makeExpression(node.right.expression, "self", temp);
            }
            addPart += "\n";
        }

    } else if (node.type === "assign") {
        //assign left is always a getter/pointer/reference!
        addPart += indent + U.makeGetterPhrase(node.left.items, "self", temp);
        addPart += " = " + U.makeExpression(node.right.expression, "self", temp) + "\n";

    } else if (node.type === "call") {
        addPart += indent + U.makeExpression(node, "self", temp) + "\n";

    } else if (node.type === "if") {
        addPart += indent + "if " + U.makeExpression(node.expression, "self", temp) + " \n";
    }

    display[0] += addPart;

    if (!node.children) {
        if (node.type === "class" || node.type === "method" || node.type === "if") {
            display[0] += "\n\n";
        }
        return;
    }
    node.children.forEach(function (child) {
        translatePy(child, level + 1, display, temp, node.id);
    });

    display[0] += "\n";
}