export default {
    parseTreeNodeIds(node, temp, parent) {
        const self = this;
        //temp[0] -> classVars:Map [classVarId, classVar]
        //temp[1] -> localVars:Map [methodId, [localVarId, localVar]]
        if (node.type === "class") {
            console.log("class not need id");
        } else if (node.type === "init") {
            if (parent.type === "class") {
                temp[0].set(node.id, node);
            } else if (parent.type === "method") {
                let localVars = temp[1].get(node.id);
                if (!localVars) {
                    localVars = new Map();
                    temp[1].set(node.id, localVars);
                }
                localVars.set(node.id, node);
            }
        } else if (node.type === "method") {
            let localVars = temp[1].get(node.id);
            if (!localVars) {
                localVars = new Map();
                temp[1].set(node.id, localVars);
            }
            if (node.params) {
                node.params.forEach(function (param) {
                    localVars.set(param.id, param);
                });
            }
        }
        if (!node.children) {
            return;
        }
        node.children.forEach(function (child) {
            self.parseTreeNodeIds(child, temp, node);
        });
    },
    getIndent(count) {
        if (count < 1) {
            return "";
        }
        let indent = "";
        while (count > 0) {
            indent += "\t";
            count -= 1;
        }
        return indent;
    },
    makeGetterPhrase(items, instanceName, variableMaps) {
        const self = this;
        let line = "";
        items.forEach(function (item, index) {
            if (item.type === "instance") {
                line += instanceName;
            }
            else if (item.type === "classVar") {
                const classVar = variableMaps[0].get(item.id);
                line += classVar.left.name;
            }
            else if (item.type === "localVar") {
                const localVar = variableMaps[1].get(item.mid).get(item.id);
                line += localVar.left.name;
            }
            else if (item.type === "value") {
                line += item.value;
            }
            else if (item.type === "new") {
                if (item.keyword) {
                    line += item.keyword + " ";
                }
                line += item.value;
            }
            else if (item.type === "static") {
                line += item.value;
            }
            else if (item.type === "call") {
                line += item.value;
                line += "("
                item.params.forEach(function (param, index, array) {
                    line += self.makeGetterPhrase(param.items, instanceName, variableMaps);
                    if (index < array.length - 1) {
                        line += ", ";
                    }
                });
                line += ")"
            }
            else if (item.type === "expression") {
                console.log("call item may have parameters as expression")
            }

            if (index < items.length - 1) {
                line += "."
            }
        });
        return line;
    },
    makeExpression(expression, instanceName, variableMaps) {
        const self = this;
        if (!expression.operator) {
            return self.makeGetterPhrase(expression.items, instanceName, variableMaps);
        }
        return "(" + self.makeExpression(expression.left, instanceName, variableMaps) + " " + expression.operator + " " + self.makeExpression(expression.right, instanceName, variableMaps) + ")";
    },
    getClassName(words) {
        let formattedName = "";
        if (words.length > 0) {
            let count = 0;
            while (count < words.length) {
                let word = words[count].toLowerCase();
                formattedName += word.charAt(0).toUpperCase() + word.slice(1);
                count += 1;
            }
        }
        return formattedName;
    },
    getReferenceName(words) {
        let formattedName = words[0].toLowerCase();
        if (words.length > 0) {
            let count = 1;
            while (count < words.length) {
                let word = words[count].toLowerCase();
                formattedName += word.charAt(0).toUpperCase() + word.slice(1);
                count += 1;
            }
        }
        return formattedName;
    }
}