<template>
<div>
    <MySVG :language="lang" />
</div>
</template>

<script>
import {
    useRoute
} from "vue-router";
import MySVG from "@/components/MySVG.vue";

export default {
    components: {
        MySVG
    },
    setup() {
        const langs = {
            javascript: {
                name: "JavaScript",
                id: "javascript"
            },
            java: {
                name: "Java",
                id: "java"
            },
            python: {
                name: "Python",
                id: "python"
            }
        };
        const route = useRoute();
        return {
            lang: langs[route.params.lang]
        };
    }
};
</script>
