import MyNode from "./MyNode.js";
import {
    SVG
} from "@svgdotjs/svg.js";

export default class MyNodeButtonV2 extends MyNode {
    constructor(parent, network, state, allPath, name) {
        super(parent, network, state, allPath, name);
        const self = this;

        const html = self.svg.foreignObject(100, 50);
        html.add(SVG('<div style="position: fixed;"><button id="' + self.id + '">Button</button></div>', true));
        html.move(50, 100);
        const button = document.getElementById(self.id);
        button.addEventListener("mouseenter", function () {
            state.canDrag = false;
        });
        button.addEventListener("mouseleave", function () {
            state.canDrag = true;
        });
        button.addEventListener("touchend", function (e) {
            e.preventDefault();
            this.click();
        });
    }

    execute() {
        this.next(this.outputs[0].id, this.selfMethod, this.lang);
    }

    setLang(lang) {
        this.lang = lang;
    }

    buildTree(tree, constructorNode) {
        const self = this;
        self.selfMethod = {
            type: "method",
            id: self.id,
            name: "buttonClicked",
            return: { class: "void" },
            children: []
        };
        tree.children.push(self.selfMethod);
        console.log(constructorNode);
    }
}