export default function (lang, paramItems) {
    if (lang === "java") {
        return [
            {
                type: "static",
                value: "System"
            },
            {
                type: "static",
                value: "out"
            },
            {
                type: "call",
                value: "println",
                params: [
                    {
                        items: paramItems
                    }
                ]
            }
        ];
    } else if (lang === "javascript") {
        return [
            {
                type: "static",
                value: "window"
            },
            {
                type: "call",
                value: "alert",
                params: [
                    {
                        items: paramItems
                    }
                ]
            }
        ];
    } else if (lang === "python") {
        return [
            {
                type: "call",
                value: "print",
                params: [
                    {
                        items: paramItems
                    }
                ]
            }
        ];
    } else {
        return [];
    }
}