import {
    v4 as uuidv4
} from "uuid";
import {
    SVG
} from "@svgdotjs/svg.js";
import Sockets from "@/values/sockets.js";

export default class MySocket {
    constructor(draw, type, flow, x, y, state, node) {
        const self = this;
        const color = Sockets[type].color;
        self.id = uuidv4();
        self.color = color;

        const socket = draw.nested();
        const polygon = socket.polygon(Sockets[type].polygon).attr({ "fill": color });
        self.polygon = polygon;
        socket.move(x, y);
        if (flow === "in" && type === Sockets.string.name) {
            const html = socket.foreignObject(50, 23);
            html.add(SVG('<div style="position: fixed;"><input type="text" id="' + self.id + '" class="socket-input-string" /></div>', true));
            html.x(40);
            const input = document.getElementById(self.id);
            input.addEventListener("input", function (e) {
                const text = e.target.value;
                self.value = "\"" + text + "\"";
            });
            self.value = "\"\"";
        } else if (flow === "in" && type === Sockets.int.name) {
            const html = socket.foreignObject(50, 23);
            html.add(SVG('<div style="position: fixed;"><input type="number" id="' + self.id + '" class="socket-input-string" /></div>', true));
            html.x(40);
            const input = document.getElementById(self.id);
            input.addEventListener("input", function (e) {
                const text = e.target.value;
                const number = parseInt(text);
                if (isNaN(number)) {
                    self.value = 0;
                } else {
                    self.value = number;
                }

            });
            self.value = 0;
        }

        self.hideInput = function () {
            if (flow === "in" && type === Sockets.string.name) {
                document.getElementById(self.id).remove();
            }
        };

        socket.on("mouseenter", function () {
            console.log("mouseenter");
            state.canDrag = false;
            polygon.fill("#0000ff");
            socket.css("cursor", "pointer");
            if (state.makingConnection) {
                if (state.endSocket !== self) {
                    if (flow === "in") {
                        state.endSocket = self;
                        console.log("endSocket");
                    }
                }
            }
        });
        socket.on("mouseleave", function () {
            state.canDrag = true;
            polygon.fill(color);
            socket.css("cursor", null);
            if (state.makingConnection) {
                state.endSocket = null;
            }
        });
        socket.on("mousedown", function () {
            if (flow === "out") {
                console.log("start to draw");
                state.makingConnection = true;
                state.startSocket = self;
            }
        });
        socket.on("touchend", function () {
            //Suport touch device to make connections
            state.canDrag = false;
            if (state.makingConnection) {
                if (state.endSocket !== self) {
                    if (flow === "in") {
                        state.endSocket = self;
                        console.log("endSocket");
                        state.finishConnection();
                    }
                }
            }
            if (flow === "out") {
                console.log("start to draw");
                polygon.fill("#0000ff");
                state.makingConnection = true;
                state.startSocket = self;
            }
        });
        socket.on("click", function () {
            console.log(self.id + "\n" + self.value);
        });

        self.flow = flow;
        self.svg = socket;
        self.type = type;
        self.node = node;
        self.rx = x + 10;
        self.ry = y + 10;
    }

    initColor() {
        this.polygon.fill(this.color);
    }
}