import {
    v4 as uuidv4
} from "uuid";
import MySocket from "@/models/MySocket";

export default class MyNode {
    constructor(parent, network, state, allPath, name) {
        const self = this;
        const node = parent.nested();
        node.rect(200, 200).attr({ "fill": "#fff", "fill-opacity": 0.7, "stroke-width": 2, "stroke": "#000", "rx": 15 });
        node.css("overflow", "visible");
        node.draggable();
        const text = node.text(name);
        text.move(80, 10);

        self.name = name;
        self.id = uuidv4();
        self.svg = node;
        self.text = text;
        self.connections = [];
        self.inputs = [];
        self.outputs = [];
        self.allPath = allPath;
        self.state = state;
        self.network = network;

        node.on("beforedrag", function (e) {
            if (!state.canDrag) {
                e.preventDefault();
            }
        });
        node.on("dragmove", function () {
            node.front();
            self.connections.forEach(function (connection) {
                const pathSvg = self.allPath.get(connection.id);
                pathSvg.plot("M " + (connection.output.node.svg.x() + connection.output.rx) + " " + (connection.output.node.svg.y() + connection.output.ry) + " L " + (connection.input.node.svg.x() + connection.input.rx) + " " + (connection.input.node.svg.y() + connection.input.ry));
            });
        });
    }

    next(outSocketId, buildingMethod, lang) {
        const nextInSocket = this.network.execConnections.get(outSocketId);
        if (nextInSocket) {
            nextInSocket.node.execute(buildingMethod, lang);
        } else {
            this.network.end();
        }
    }

    addSocket(type, flow) {
        const self = this;
        var socketX = -10;
        var socketY = 20;
        if (flow === "out") {
            socketX = 190;
            socketY += 40 * self.outputs.length;
        } else {
            socketY += 40 * self.inputs.length;
        }

        const socket = new MySocket(self.svg, type, flow, socketX, socketY, self.state, self);
        if (flow === "out") {
            self.outputs.push(socket);
        } else {
            self.inputs.push(socket);
        }
    }

    addConnection(id, output, input) {
        this.connections.push({ id, output, input });
    }
}