import U from "@/logics/utils.js";

export function translateJava(node, level, display, temp, parentId) {
    //node -> JSON node tree
    //level -> 0
    //display -> [""]
    //temp[0] -> classVars:Map [classVarId, classVar]
    //temp[1] -> localVars:Map [methodId, [localVarId, localVar]]
    //parentId -> might be undefined
    let indent = U.getIndent(level);
    let addPart = "";

    if (node.type === "class") {
        addPart += indent + "public " + node.type + " " + node.name + " {\n";
    } else if (node.type === "method") {
        if (node.return) {
            addPart += indent + "public " + node.return.class + " " + node.name + "(";
        } else {
            addPart += indent + "public " + node.name + "(";
        }
        if (node.params) {
            node.params.forEach(function (param, index) {
                if (index > 0) {
                    addPart += ", "
                }
                addPart += param.class + " " + param.name;
            });
        }
        addPart += ") {\n";

    } else if (node.type === "init") {
        if (parentId === "class") {
            addPart += indent + "public " + node.left.class + " " + node.left.name + ";\n";
        } else {
            addPart += indent + node.left.class + " " + node.left.name;
            if (node.right) {
                addPart += " = " + U.makeExpression(node.right.expression, "this", temp);
            }
            addPart += ";\n";
        }

    } else if (node.type === "assign") {
        //assign left is always a getter/pointer/reference!
        addPart += indent + U.makeGetterPhrase(node.left.items, "this", temp);
        addPart += " = " + U.makeExpression(node.right.expression, "this", temp) + ";\n";

    } else if (node.type === "call") {
        addPart += indent + U.makeExpression(node, "this", temp) + ";\n";

    } else if (node.type === "if") {
        addPart += indent + "if " + U.makeExpression(node.expression, "this", temp) + " {\n";
    }

    display[0] += addPart;

    if (!node.children) {
        if (node.type === "class" || node.type === "method" || node.type === "if") {
            display[0] += indent + "}\n";
        }
        return;
    }
    node.children.forEach(function (child) {
        translateJava(child, level + 1, display, temp, node.id);
    });

    display[0] += indent + "}\n";
}