import MyNode from "./MyNode.js";

export default class MyNodeVarString extends MyNode {
    execute(buildingMethod, lang) {
        const self = this;
        if (self.network.valueConnections.has(self.inputs[1].id)) {
            console.log("ok I will find value");
        } else {
            const socketValue = self.inputs[1].value;
            buildingMethod.children.push({
                type: "assign",
                left: {
                    items: [
                        {
                            type: "instance"
                        },
                        {
                            type: "classVar",
                            id: self.id
                        }
                    ]
                },
                right: {
                    expression: {
                        items: [
                            {
                                type: "value",
                                value: socketValue
                            }
                        ]
                    }
                }
            });
            self.next(self.outputs[0].id, buildingMethod, lang);
        }
    }

    buildTree(tree, constructorNode) {
        const self = this;
        tree.children.push({
            type: "init",
            id: self.id,
            left: {
                class: "String",
                name: self.name
            }
        });
        constructorNode.children.push({
            type: "assign",
            left: {
                items: [{
                    type: "instance"
                },
                {
                    type: "classVar",
                    id: self.id
                }
                ]
            },
            right: {
                expression: {
                    items: [{
                        type: "value",
                        value: "\"\""
                    }]
                }
            }
        });
    }
}