import MyNode from "./MyNode.js";
import getItemsFromLanguage from "./MyNodeFunctionAlert_langs";

export default class MyNodeFunctionAlert extends MyNode {
    execute(buildingMethod, lang) {
        const self = this;
        const paramItems = [];
        if (self.network.valueConnections.has(self.inputs[1].id)) {
            paramItems.push({ type: "instance" });
            paramItems.push({ type: "classVar" });
            self.trace(self.inputs[1].id, paramItems[1]);//expression
        } else {
            paramItems.push({ type: "value", value: self.inputs[1].value });
        }

        const callItems = getItemsFromLanguage(lang, paramItems);

        buildingMethod.children.push({
            type: "call",
            items: callItems
        });
        self.next(self.outputs[0].id, buildingMethod, lang);
    }

    trace(inSocketId, item) {
        const self = this;
        const lastOutSocket = self.network.valueConnections.get(inSocketId);
        item.id = lastOutSocket.node.id;
        //continue trace
    }
}